
#app {
  width: 100%;
  height: 100%;
}
#app .picker-show {
  display: block;
  width: 98%;
  height: .44rem;
  border-radius: .06rem;
  margin: .1rem auto;
  background-color: #20a0ff;
  color: #fff;
  font-size: .16rem;
  line-height: .44rem;
}
#app .picker-show .left,
#app .picker-show .right {
  margin: 0;
  box-sizing: border-box;
  padding: 0 .2rem;
  line-height: .44rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
#app .picker-show .left {
  float: left;
}
#app .picker-show .right {
  float: right;
}



